const StatusDisplay = ({ user, ClassName, cancelStatusScreen }) => {
	let userImage = user.picture ? user.picture.thumbnail : null;
	let userImage1 = user.picture ? user.picture.large : null;

	return (
		<div
			style={{
				backgroundImage: `url(${userImage})`,
				zIndex: "15",
				height: "100vh",
				width: "100%",
				position: "absolute",
				top: 0,
				left: 0,
				backgroundPosition: "center",
				backgroundSize: "cover"
			}} 
			className={ClassName}
		>
		<div className="cancel_status-screen" onClick={cancelStatusScreen} >X</div>
			<img src={userImage1}/>
			<div className="status_display-header">
				<div className="chat_list-item">
					<div>
						<div className="chat_list-item-left">
							<svg
								className="chat_list-item-svg"
								width="48"
								height="48"
								viewBox="0 0 104 104"
							>
								<circle
									className="svg-circle"
									cx="52"
									cy="52"
									r="50"
									fill="black"
									stroke-linecap="round"
									stroke-width="4"
									stroke-dashoffset="387.69908169872417"
								></circle>
							</svg>
							<div className="chats_status-img"
								style={{
										backgroundImage: `url(${userImage})`,
										height: "40px",
										width: "40px",
										borderRadius: "50%"
									}
								}
							></div>
						</div>
						{
							user.name ?
								<div className="chat_list-item-right">
									<p className="chat_list-name">{`${user.name.first} ${user.name.last}`}</p>
									<p className="chat_list-time">today at {(new Date()).getHours() + ":" + (new Date()).getMinutes()}</p>
								</div>
								:
								null
						}
					</div>
				</div>
			</div>
		</div>
	);
}

export default StatusDisplay;